import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import DateRangePickerString from 'components/common/input/DateRangePickerString';
import SelectMultiAjax from 'components/common/input/SelectMultiAjax';
import { BtnSearch, BtnClear } from 'components/common/button';
import { searchSubject } from '../../../services/classCredit';
import { getTestSessions } from '../../../services/testSession';
import { requiredRule } from '../../../utils/validateRules';
import SelectAjax from '../../../components/common/input/SelectAjax';
import { updateQueryParams, getQueryParams } from '../../../utils/urlUtil';

const { Option } = Select;

const optionFieldDecorator = {
  trainTypeId: {
    rules: [requiredRule],
  },
  facultyId: {
    rules: [requiredRule],
  },
  courseId: {
    rules: [requiredRule],
  },
  semesterId: {
    rules: [requiredRule],
  },
  yearEduId: {
    rules: [requiredRule],
  },
};

@Form.create({ name: 'FormSearch' })
class FormSearch extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    const params = getQueryParams();
    if (params.subjectIds) {
      params.subjectIds = params.subjectIds.split(',');
    }
    form.setFieldsValue(params);
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const courseId = getFieldValue('courseId');
    const studyFieldId = getFieldValue('studyFieldId');
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    // const classId = getFieldValue('class');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <Col span={10}>
            <Form.Item label="Mn hc" hasFeedback>
              {getFieldDecorator('subjectIds', {
                rules: [requiredRule],
              })(
                <SelectMultiAjax
                  keyValue="subjectId"
                  keyLabel="subjectLabel"
                  service={searchSubject}
                  allowGetData={!!semesterId}
                  params={{ trainTypeId, facultyId, courseId, studyFieldId, yearEduId, semesterId }}
                  form={this.props.form}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="t thi">
              {getFieldDecorator('examBatch', { initialValue: '' })(
                <Select allowClear>
                  {[1, 2, 3, 4].map(i => {
                    return <Option value={i}>{i}</Option>;
                  })}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Ln thi">
              {getFieldDecorator('examTime', { initialValue: '' })(
                <Select allowClear>
                  {[1, 2, 3, 4].map(i => {
                    return <Option value={i}>{i}</Option>;
                  })}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ngy thi" hasFeedback>
              {getFieldDecorator('rangeDate')(
                <DateRangePickerString
                  form={this.props.form}
                  fromField="fromDate"
                  toField="toDate"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ca thi">
              {getFieldDecorator('testSessionId', { initialValue: '' })(
                <SelectAjax service={getTestSessions} />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearch;



