import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Modal, Tooltip } from 'antd';
import { CartHeadAction, ColEditDelete, TableData } from 'components/common';
import { Btn, BtnAddNew, BtnExport, BtnPrint } from 'components/common/button';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';
import {
  getTestOrganizings,
  deleteTestOrganizing,
  exportExcelTestRoomStudents,
  exportPdfTestRoomStudents,
} from 'services/testOrganizing';

import FromSearch from './FormSearch';

import TeacherExam from './TeacherExam';
import { getQueryParams } from '../../../utils/urlUtil';
import { dateToString } from '../../../utils/formatUtil';

const ROUTER_FORM_DATA = routeConstant.TO_CHUC_THI__QUAN_LY_LICH_THI__NHAP_DU_LIEU;
class ExamOrganization extends Component {
  state = { visible: false, paramSearch: {}, recordEdit: {}, reload: 1 };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    const paramSearch = getQueryParams();
    if (paramSearch.subjectIds) {
      paramSearch.subjectIds = paramSearch.subjectIds.split(',');
    }
    this.setState({ paramSearch });
  };

  showModalTeacherExam = record => {
    this.setState({
      visible: true,
      recordEdit: record,
    });
  };

  handleUpdateSupervisersSucess = () => {
    this.setState({
      visible: false,
      reload: this.state.reload + 1,
    });
  };

  handleCancel = () => {
    this.setState({
      visible: false,
      recordEdit: {},
    });
  };

  handleDelete = async id => {
    await deleteTestOrganizing({ id });
    this.setState({
      reload: this.state.reload + 1,
    });
  };

  downloadExcel = async record => {
    await exportExcelTestRoomStudents({ torId: record.id });
  };

  printPdf = async record => {
    await exportPdfTestRoomStudents({ torId: record.id });
  };

  render() {
    const columns = [
      {
        title: 'K hiu',
        dataIndex: 'subjectId',
        key: 'subjectId',
      },
      {
        title: 'Tn mn',
        dataIndex: 'subject',
        key: 'subject',
      },
      {
        title: 't thi',
        dataIndex: 'examBatch',
        key: 'examBatch',
      },
      {
        title: 'Ln thi',
        dataIndex: 'examTime',
        key: 'examTime',
      },
      {
        title: 'Ngy thi',
        dataIndex: 'testDate',
        key: 'testDate',
        render: value => dateToString(value),
      },
      {
        title: 'Ca thi',
        dataIndex: 'testSession',
        key: 'testSession',
      },
      {
        title: 'Gi thi',
        dataIndex: 'testTime',
        key: 'testTime',
      },
      {
        title: 'S SV',
        dataIndex: 'noOfStudent',
        key: 'noOfStudent',
      },
      {
        title: 'Phng thi',
        dataIndex: 'room',
        key: 'room',
      },
      {
        title: 'Gim th 1',
        dataIndex: 'superviser1',
        key: 'superviser1',
      },
      {
        title: 'Gim th 2',
        dataIndex: 'superviser2',
        key: 'superviser2',
      },
      {
        title: 'Gim th 3',
        dataIndex: 'superviser3',
        key: 'superviser3',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <Fragment>
            <ColEditDelete
              linkEdit={{
                to: {
                  pathname: ROUTER_FORM_DATA,
                  search: `?toId=${record.toId}`,
                },
              }}
              onConfirmDelete={() => this.handleDelete(record.toId)}
            />
            <Tooltip placement="top" title="Phn GT">
              <Btn
                style={{ marginTop: 5 }}
                icon="user-o"
                onClick={() => this.showModalTeacherExam(record)}
              />
            </Tooltip>
            <Tooltip placement="top" title="Xut danh sch">
              <BtnExport
                onClick={() => this.downloadExcel(record)}
                style={{ marginTop: 5 }}
                title=""
              />
            </Tooltip>
            <Tooltip placement="top" title="In danh sch">
              <BtnPrint onClick={() => this.printPdf(record)} style={{ marginTop: 5 }} title="" />
            </Tooltip>
          </Fragment>
        ),
      },
    ];
    const { paramSearch } = this.state;
    return (
      <Card title="T chc thi">
        <CartHeadAction>
          <Link to={ROUTER_FORM_DATA}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FromSearch />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getTestOrganizings}
              allowGetData={!!paramSearch.courseId && !!paramSearch.subjectIds}
              paramSearch={paramSearch}
              bordered
              columns={columns}
            />
          </Col>
        </Row>
        <Modal
          title="Phn gim th"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          style={{ top: 20 }}
          footer={null}
        >
          <TeacherExam
            onSubmitSucess={this.handleUpdateSupervisersSucess}
            recordEdit={this.state.recordEdit}
            visible={this.state.visible}
            onCancel={this.handleCancel}
          />
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ExamOrganization />
  </Page>
);



